import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../store/store.js";
import { Contact } from "../store/types.js";
import { deleteContact } from "../store/reducer";
import { Button, ListGroup } from "react-bootstrap";

interface Props {
  filterType: string;
}

const ContactList: React.FC<Props> = ({ filterType }) => {
  const contacts = useSelector(
    (state: RootState) => state.contacts.contactList
  );
  const dispatch = useDispatch();

  const filtered =
    filterType === "all"
      ? contacts
      : contacts.filter((c) => c.type === filterType);

  const grouped: Record<string, Contact[]> = {};
  filtered.forEach((c) => {
    if (!grouped[c.type]) grouped[c.type] = [];
    grouped[c.type].push(c);
  });

  return (
    <>
      {Object.entries(grouped).map(([type, items]) => (
        <div key={type}>
          <h5>{type.toUpperCase()}</h5>
          <ListGroup className="mb-3">
            {items.map((c) => (
              <ListGroup.Item
                key={c.id}
                className="d-flex justify-content-between align-items-center"
              >
                <div>
                  <strong>{c.value}</strong> - {c.description}
                </div>
                <Button
                  variant="danger"
                  size="sm"
                  onClick={() => dispatch(deleteContact(c.id))}
                >
                  Delete
                </Button>
              </ListGroup.Item>
            ))}
          </ListGroup>
        </div>
      ))}
    </>
  );
};

export default ContactList;
