import React from "react";
import { useForm } from "react-hook-form";
import { Button, Form } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { addContact } from "../store/reducer";
import ContactTypeSelect from "../atoms/ContactTypeSelect.js";
import TextInput from "../atoms/TextInput.jsx";
import DescriptionField from "../atoms/DescriptionField.jsx";
import { ContactType } from "../store/types.js";

interface Props {
  handleClose: () => void;
  handleAlert: (msg: string) => void;
}

interface FormValues {
  type: ContactType;
  value: string;
  description: string;
}

const ContactForm: React.FC<Props> = ({ handleClose, handleAlert }) => {
  const dispatch = useDispatch();
  const { register, handleSubmit, reset } = useForm<FormValues>({
    defaultValues: { type: "phone", value: "", description: "" },
  });

  const onSubmit = (data: FormValues) => {
    dispatch(addContact(data));
    handleAlert("Contact added!");
    reset();
    setTimeout(handleClose, 1500);
  };

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <Form.Group className="mb-2">
        <Form.Label>Type</Form.Label>
        <ContactTypeSelect
          value={"phone"}
          onChange={() => {}}
          {...register("type")}
        />
      </Form.Group>
      <Form.Group className="mb-2">
        <Form.Label>Contact</Form.Label>
        <TextInput value="" onChange={() => {}} {...register("value")} />
      </Form.Group>
      <Form.Group className="mb-2">
        <Form.Label>Description</Form.Label>
        <DescriptionField
          value=""
          onChange={() => {}}
          {...register("description")}
        />
      </Form.Group>
      <Button type="submit">Submit</Button>
    </Form>
  );
};

export default ContactForm;
