import React, { useState } from "react";
import { Offcanvas } from "react-bootstrap";
import ContactForm from "../molecules/ContactForm";
import FilterPanel from "../organisms/FilterPanel";
import ContactList from "../organisms/ContactList";

interface Props {
  show: boolean;
  handleClose: () => void;
  handleAlert: (msg: string) => void;
}

const ContactBookTemplate: React.FC<Props> = ({
  show,
  handleClose,
  handleAlert,
}) => {
  const [filterType, setFilterType] = useState<string>("all");

  return (
    <>
      <FilterPanel filterType={filterType} setFilterType={setFilterType} />
      <ContactList filterType={filterType} />

      <Offcanvas show={show} onHide={handleClose} placement="end">
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Add/Edit Contact</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <ContactForm handleClose={handleClose} handleAlert={handleAlert} />
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export default ContactBookTemplate;
